import {SellerProductService} from './../../../../../../../../core/services/seller/seller-product/seller-product.service';
import {Component, OnInit} from '@angular/core';
import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {SellerCreateGroupProductViewModel} from 'src/app/core/models/common/group-product/GroupProduct.ViewModel';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {debounceTime} from 'rxjs/internal/operators/debounceTime';
import {ProductSelect} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';

@Component({
	selector: 'app-seller-group-product-create',
	templateUrl: './seller-group-product-create.component.html',
	styleUrls: ['./seller-group-product-create.component.less'],
	providers: [DestroyService]
})
export class SellerGroupProductCreateComponent implements OnInit {
	uploading = false;
	public editor = ClassicEditor;
	ckEditor: string;
	groupProductForm: FormGroup;
	isSearchGroupProductsSelectLoading = false;
	groupPrductSelectList: Array<ProductSelect> = [];

	constructor(
		private formBuilder: FormBuilder,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler,
		private notificationService: NotificationService,
		private spinnerService: SpinnerService,
		private sellerProductService: SellerProductService
	) {}

	ngOnInit(): void {
		this.initForm();
	}
	initForm() {
		this.groupProductForm = this.formBuilder.group({
			groupProductName: ['', [Validators.required, Validators.minLength(3)]],
			groupProductDescription: ['', [Validators.required, Validators.minLength(3)]],
			groupProduct: [[]],
			saleOff: ['', [Validators.required, Validators.minLength(1)]]
		});
	}
	getFormData() {
		const val = this.groupProductForm.value;
		const createProductViewModel: SellerCreateGroupProductViewModel = {
			groupProductName: val.groupProductName,
			groupProductDescription: val.groupProductDescription,
			saleOff: val.saleOff,
			products: val.groupProduct
		};
		return createProductViewModel;
	}
	onSearchProduct(value: string) {
		console.log(value);
		this.initGroupProductList(value);
	}
	initGroupProductList(searchKeyWord: string) {
		console.log('GetCategoryVariationByCatgoryId');
		this.isSearchGroupProductsSelectLoading = true;
		this.sellerProductService
			.getSearchProductsSelect({searchKeyWord: searchKeyWord})
			.pipe(takeUntil(this.destroy$), debounceTime(500))
			.subscribe((result) => {
				console.log(result);
				this.groupPrductSelectList = result.resultObj;
				this.isSearchGroupProductsSelectLoading = false;
				console.log(this.groupPrductSelectList);
			});
	}
	submitForm(): void {
		this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		try {
			this.sellerProductService
				.createGroupProduct(this.getFormData())
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(res) => {
						//console.log(res);
						if (res.isSuccessed) {
							if (res.message) {
								this.notificationService.showSuccessNotification(res.message);
							}
						} else {
							if (res.message) {
								this.notificationService.showErrorNotification(res.message);
							}
						}
						this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					},
					() => {
						this.notificationService.showErrorNotification('Có Lỗi Xảy Ra, Vui Lòng Thử Lại');
						this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					}
				);
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}
}
