import {Component, OnInit} from '@angular/core';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {SellerProductService} from 'src/app/core/services/seller/seller-product/seller-product.service';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {SellerSearchGroupProductViewModel} from 'src/app/core/models/common/group-product/GroupProduct.ViewModel';
@Component({
	selector: 'app-seller-group-product-index',
	templateUrl: './seller-group-product-index.component.html',
	styleUrls: ['./seller-group-product-index.component.less'],
	providers: [DestroyService]
})
export class SellerGroupProductIndexComponent implements OnInit {
	pagingResult: Array<SellerSearchGroupProductViewModel>;
	pageIndex: number = 1;
	searchKeyWord: string;
	isLoading = true;
	groupProductSearchForm: FormGroup;
	constructor(
		private destroy$: DestroyService,
		private notificationService: NotificationService,
		private productService: SellerProductService,
		private formBuilder: FormBuilder
	) {}

	ngOnInit(): void {
		this.get();
		this.initSearchForm();
		this.checkSearchFormValueChange();
	}
	initSearchForm() {
		this.groupProductSearchForm = this.formBuilder.group({
			search: ['', [Validators.required, Validators.minLength(1)]]
		});
	}
	get() {
		this.productService
			.getSearchGroupProducts({
				searchKeyWord: this.searchKeyWord ?? '',
				languageCode: 'vn'
			})
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				console.log(result);
				if (result) {
					if (result.resultObj) {
						if (result.isSuccessed) {
							this.pagingResult = result.resultObj;
							console.log(this.pagingResult);
							this.isLoading = false;
						} else {
						}
					} else {
					}
				} else {
				}
			});
	}
	pageChanged(page: number) {
		this.pageIndex = page; // receive the page number
		// display the sort button by previous sort
		this.get(); // navigated by sort and page
	}
	// changeStatus(status: boolean, authorId: number) {
	// 	console.log(status);
	// 	this.authorAdminService
	// 		.changeStatus({authorId: authorId, status: status})
	// 		.pipe(takeUntil(this.destroy$))
	// 		.subscribe((result) => {
	// 			console.log(result);
	// 			if (result) {
	// 				if (result.resultObj) {
	// 					if (result.isSuccessed) {
	// 						this.notificationService.showSuccessNotification(result.message);
	// 					} else {
	// 						this.notificationService.showErrorNotification(result.message);
	// 					}
	// 				}
	// 			}
	// 		});
	// }
	checkSearchFormValueChange() {
		this.groupProductSearchForm.valueChanges.subscribe((result) => {
			console.log(result.search);
			this.searchKeyWord = result.search;
			this.pageIndex = 1;
			this.get();
		});
	}
	delete(groupProductId: number) {
		console.log(groupProductId);
		this.productService
			.deleteGroupProducts(groupProductId)
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				console.log(result);
				if (result) {
					if (result.resultObj) {
						if (result.isSuccessed) {
							this.notificationService.showSuccessNotification('Xóa Thành Công');
						} else {
						}
					} else {
						this.notificationService.showSuccessNotification(result.message);
					}
				} else {
				}
			});
	}
	cancel() {}
}
