import {SellerGroupProductEditComponent} from './components/seller-group-product-edit/seller-group-product-edit.component';
import {SellerGroupProductIndexComponent} from './components/seller-group-product-index/seller-group-product-index.component';
import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {SellerGroupProductCreateComponent} from './components/seller-group-product-create/seller-group-product-create.component';

const routes: Routes = [
	{
		path: '',
		component: SellerGroupProductIndexComponent
	},
	{path: 'create', component: SellerGroupProductCreateComponent},
	{path: 'edit/:groupProductId', component: SellerGroupProductEditComponent}
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class SellerGroupProductRoutingModule {}
